import { AuthService } from 'src/app/services/auth.service';
import { ModalController, NavController } from '@ionic/angular';
import { NgForm } from '@angular/forms';
import { Component, OnInit } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { HttpClient, HttpHeaders, HttpParams } from '@angular/common/http';
import { EnvService } from 'src/app/services/env.service';
@Component({
  selector: 'app-requestregister',
  templateUrl: './requestregister.page.html',
  styleUrls: ['./requestregister.page.scss'],
})
export class RequestregisterPage implements OnInit {
  room: string;
  part: string;
  name: string;
  email: string;
  wing: string;
  mobileNo: string;
  residence: string;
  iroom: string;
  ipart: string;
  iname: string;
  iemail: string;
  iwing: string;
  imobileNo: string;
  iresidence: string;
  constructor(private modalController: ModalController,
              private http: HttpClient,
              private env: EnvService,
  ) {
    const myDataEmail = localStorage['objectToPassEmail'];
    localStorage.removeItem('objectToPassEmail');
    const myDataPhone = localStorage['objectToPassPhone'];
    localStorage.removeItem('objectToPassPhone');
    if (myDataEmail != null && myDataPhone != null) {
      const headers = new HttpHeaders().set('Content-Type', 'application/json');
      const params = new HttpParams().set('email', myDataEmail).set('mobileNo', myDataPhone);
      this.http.get(this.env.API_URL + 'users/isUser', { headers, params }).subscribe(
        data => {
          this.email = data[0].email;
          this.room = data[0].room;
          this.name = data[0].name;
          this.part = data[0].part;
          this.wing = data[0].wing;
          this.mobileNo = data[0].mobileNo;
          this.residence = data[0].residence;
        }, err => {
          console.log(err);
        }
      );
    } else {

    }
  }
  ngOnInit() {

  }

  dismissReqRegister() {
    this.modalController.dismiss();
  }
  requestRegistration() {
    this.http.post(this.env.API_URL + 'users/register', { email: this.iemail, mobileNo: this.imobileNo, name: this.iname,
                   residence: this.iresidence, wing: this.iwing, room: this.iroom, part: this.ipart }).subscribe(
      res => {
        console.log(res
        );
      },
      err => {
        console.log('Error occured');
      }
    );

    const dvId = Math.floor(Math.random() * 20000);
    const dvType = 'Android';
    const dvName = 'SamSung s6';

    this.http.post(this.env.API_URL + 'notti/registerDevice', { deviceId: dvId, deviceType: dvType, deviceName: dvName,
      deviceLastLogin: '2019-02-23', deviceIp: '192.168.12.1' }).subscribe(
      res => {
        console.log(res);
      },
      err => {
        console.log('Error occured');
      }
    );

    this.http.post(this.env.API_URL + 'noti/registerNoti', { userEmail: this.iemail, deviceId: dvId, deviceType: dvType,
      deviceName: dvName, tokenId: '54684552451215151' }).subscribe(
      res => {
        console.log(res);
      },
      err => {
        console.log('Error occured');
      }
    );
  }
}
